import ECharts from 'vue-echarts'
import 'echarts/lib/chart/pie'
import 'echarts/lib/component/tooltip'
import { fmtMoney } from '@/filters'

export default {
  components: {
    'v-chart': ECharts
  },
  props: {
    datas: {
      type: Array,
      default: () => []
    }
  },
  data () {
    return {
      options: { // 
        tooltip: {
          trigger: 'item',
          formatter: '{b}<br/>{c}<br/>{d}%',
          backgroundColor: '#fff',
          borderColor: '#909399',
          borderWidth: 1,
          textStyle: {
            color: '#303133',
            fontSize: 13
          }
        },
        series: []
      },
      totalProperty: 'integral', // 
      chartValue: 'integral' // 
    }
  },
  computed: {
    total () { // 
      return this.datas.reduce((sum, cur) => sum + cur[this.totalProperty], 0)
    }
  },
  watch: {
    datas: { // 
      handler: function (val) {
        this.options.series[0].data = val.map(d => {
          return {
            value: d[this.chartValue],
            name: d.name
          }
        })
      },
      immediate: true,
      deep: true
    }
  },
  methods: {
    getSummaries (param) { // 
      const { columns, data } = param
      const sums = []

      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = ''
          return
        }
        const values = data.map(item => Number(item[column.property]))
        if (this.isMatchCondition(column) && !values.every(value => isNaN(value))) {
          sums[index] = fmtMoney(values.reduce((prev, curr) => {
            const value = Number(curr)
            if (!isNaN(value)) {
              return prev + curr
            } else {
              return prev
            }
          }, 0))
        } else {
          sums[index] = ''
        }
      })

      return sums
    },

    isMatchCondition (column) { // 
      return true
    }
  }
}
